"use client";

import { useEffect, useMemo, useState } from "react";
import { api } from "@/lib/api";

import {
  ResponsiveContainer,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";

type Row = {
  provider: string;
  impressions: number | string;
  revenue: number | string;
  cost?: number | string;
  profit?: number | string;
  cpm?: number | string;
};

const PERIOD_LABELS = {
  today: "–°–µ–≥–æ–¥–Ω—è",
  "7d": "7 –¥–Ω–µ–π",
  "30d": "30 –¥–Ω–µ–π",
} as const;

export default function AnalyticsPage() {
  const [period, setPeriod] = useState<"today" | "7d" | "30d">("today");
  const [from, setFrom] = useState<string>("");
  const [to, setTo] = useState<string>("");

  const [rows, setRows] = useState<Row[]>([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    setLoading(true);

    const query =
      from && to
        ? `/admin/stats/providers?from=${from}&to=${to}`
        : `/admin/stats/providers?period=${period}`;

    api(query)
      .then((r) => setRows(r?.stats ?? []))
      .finally(() => setLoading(false));
  }, [period, from, to]);

  const totals = useMemo(() => {
    const totalImpressions = rows.reduce(
      (sum, r) => sum + Number(r.impressions || 0),
      0
    );

    const totalRevenue = rows.reduce(
      (sum, r) => sum + Number(r.revenue || 0),
      0
    );

    const totalCost = rows.reduce(
      (sum, r) => sum + Number(r.cost || 0),
      0
    );

    const totalProfit = totalRevenue - totalCost;

    const cpm =
      totalImpressions > 0 ? (totalRevenue / totalImpressions) * 1000 : 0;

    return {
      totalImpressions,
      totalCost,
      totalProfit,
      cpm,
    };
  }, [rows]);

  const chartData = useMemo(
    () =>
      rows.map((r) => ({
        provider: r.provider,
        revenue: Number(r.revenue || 0),
      })),
    [rows]
  );

  function showCPM(r: Row) {
    const imps = Number(r.impressions || 0);
    if (imps < 10) return "‚Äî";
    if (r.cpm !== undefined) return `$${Number(r.cpm).toFixed(2)}`;
    return `$${((Number(r.revenue || 0) / imps) * 1000).toFixed(2)}`;
  }

  return (
    <div className="page-inner">
      <div className="page-header">
        <div>
          <h1 className="page-title">–ê–Ω–∞–ª–∏—Ç–∏–∫–∞</h1>
          <p className="page-subtitle">
            –≠—Ñ—Ñ–µ–∫—Ç–∏–≤–Ω–æ—Å—Ç—å –ø—Ä–æ–≤–∞–π–¥–µ—Ä–æ–≤ –∑–∞ –≤—ã–±—Ä–∞–Ω–Ω—ã–π –ø–µ—Ä–∏–æ–¥
          </p>
        </div>
      </div>

      <div className="split-layout">
        <aside className="side-panel">
          <div className="panel-title">–û–±–∑–æ—Ä</div>

          <div className="stats-list">
            <div className="stat-row">
              <span>–ü–µ—Ä–∏–æ–¥</span>
              <strong>
                {from && to ? `${from} ‚Äî ${to}` : PERIOD_LABELS[period]}
              </strong>
            </div>

            <div className="stat-row">
              <span>–ü—Ä–æ–≤–∞–π–¥–µ—Ä—ã</span>
              <strong>{rows.length}</strong>
            </div>

            <div className="stat-row">
              <span>–†–∞—Å—Ö–æ–¥</span>
              <strong>${totals.totalCost.toFixed(2)}</strong>
            </div>

            <div className="stat-row">
              <span>–ü—Ä–∏–±—ã–ª—å</span>
              <strong>${totals.totalProfit.toFixed(2)}</strong>
            </div>

            <div className="stat-row">
              <span>CPM</span>
              <strong>
                {totals.totalImpressions < 10
                  ? "‚Äî"
                  : `$${totals.cpm.toFixed(2)}`}
              </strong>
            </div>
          </div>

          <div className="panel-divider" />

          <div className="panel-title">–ü–µ—Ä–∏–æ–¥</div>

          {/* –ö–ù–û–ü–ö–ò */}
          <div className="analytics-filters">
            {(["today", "7d", "30d"] as const).map((p) => (
              <button
                key={p}
                className={`filter ${period === p ? "active" : ""}`}
                onClick={() => {
                  setPeriod(p);
                  setFrom("");
                  setTo("");
                }}
              >
                {PERIOD_LABELS[p]}
              </button>
            ))}
          </div>

          {/* –û–¢ ‚Äî –î–û –í –û–î–ù–£ –õ–ò–ù–ò–Æ */}
          <div className="date-range-line">
            <input
              type="date"
              value={from}
              onChange={(e) => {
                setFrom(e.target.value);
                setPeriod("today");
              }}
            />
            <span className="dash">‚Äî</span>
            <input
              type="date"
              value={to}
              onChange={(e) => {
                setTo(e.target.value);
                setPeriod("today");
              }}
            />
          </div>
        </aside>

        <section className="main-panel">
          <div className="chart-card">
            <div className="chart-head">
              <div className="chart-title">–î–æ—Ö–æ–¥ –ø–æ –ø—Ä–æ–≤–∞–π–¥–µ—Ä–∞–º</div>
              <div className="chart-sub">
                {from && to ? `${from} ‚Äî ${to}` : PERIOD_LABELS[period]}
              </div>
            </div>

            <div className="chart-box">
              {loading ? (
                <div className="loading">–ó–∞–≥—Ä—É–∑–∫–∞‚Ä¶</div>
              ) : !mounted ? null : chartData.length === 0 ? (
                <div className="empty">–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö –∑–∞ –≤—ã–±—Ä–∞–Ω–Ω—ã–π –ø–µ—Ä–∏–æ–¥</div>
              ) : (
                <ResponsiveContainer width="100%" height={260}>
                  <BarChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="provider" />
                    <YAxis />
                    <Tooltip />
                    <Bar
                      dataKey="revenue"
                      fill="#38bdf8"
                      radius={[6, 6, 0, 0]}
                    />
                  </BarChart>
                </ResponsiveContainer>
              )}
            </div>
          </div>

          <div className="table-card">
            <table className="table">
              <thead>
                <tr>
                  <th>–ü—Ä–æ–≤–∞–π–¥–µ—Ä</th>
                  <th>–ü–æ–∫–∞–∑—ã</th>
                  <th>–î–æ—Ö–æ–¥ ($)</th>
                  <th>–†–∞—Å—Ö–æ–¥ ($)</th>
                  <th>–ü—Ä–∏–±—ã–ª—å ($)</th>
                  <th>CPM ($)</th>
                </tr>
              </thead>

              <tbody>
                {rows.map((r) => (
                  <tr key={r.provider}>
                    <td>{r.provider}</td>
                    <td>{Number(r.impressions || 0)}</td>
                    <td>${Number(r.revenue || 0).toFixed(2)}</td>
                    <td>${Number(r.cost || 0).toFixed(2)}</td>
                    <td>
                      $
                      {Number(
                        r.profit ??
                          Number(r.revenue || 0) - Number(r.cost || 0)
                      ).toFixed(2)}
                    </td>
                    <td>{showCPM(r)}</td>
                  </tr>
                ))}
              </tbody>
            </table>

            {loading && <div className="loading">–ó–∞–≥—Ä—É–∑–∫–∞‚Ä¶</div>}
          </div>
        </section>
      </div>
    </div>
  );
}
